/*******************************
           Rating
*******************************/

.ui.rating {
  display: inline-block;
  vertical-align: middle;
}
.ui.rating:last-child {
  margin-right: 0em;
}
.ui.rating:before {
  display: block;
  content: '';
  visibility: hidden;
  clear: both;
  height: 0;
}

/* Icon */
.ui.rating .icon {
  cursor: pointer;
  margin: 0em;
  width: 1.1em;
  height: auto;
  text-align: center;
  padding: 0em;
  font-weight: normal;
  font-style: normal;
}


/*******************************
             Types
*******************************/


/*-------------------
        Star
--------------------*/


/* Inactive */
.ui.star.rating .icon {
  width: 1.1em;
  color: rgba(0, 0, 0, 0.15);
}

/* Active Star */
.ui.star.rating .active.icon {
  color: #ffe623 !important;
  text-shadow: 0px -1px 0px #cfa300, -1px 0px 0px #cfa300, 0px 1px 0px #cfa300, 1px 0px 0px #cfa300;
}

/* Selected Star */
.ui.star.rating .icon.selected,
.ui.star.rating .icon.selected.active {
  color: #ffb70a !important;
}
.ui.star.rating.partial {
  position: relative;
  z-index: 1;
}
.ui.star.rating.partial:before {
  position: absolute;
  z-index: -1;
}

/*-------------------
        Heart
--------------------*/

.ui.heart.rating .icon {
  width: 1.25em;
  color: rgba(0, 0, 0, 0.15);
}

/* Active Heart */
.ui.heart.rating .active.icon {
  color: #ff2733 !important;
  text-shadow: 0px -1px 0px #9e0000, -1px 0px 0px #9e0000, 0px 1px 0px #9e0000, 1px 0px 0px #9e0000;
}

/* Selected Heart */
.ui.heart.rating .icon.selected,
.ui.heart.rating .icon.selected.active {
  color: #ff2733 !important;
}


/*******************************
             States
*******************************/


/* Inactive Icon */
.ui.rating .icon {
  color: rgba(0, 0, 0, 0.15);
}

/* Active Icon */
.ui.rating .active.icon {
  color: rgba(0, 0, 0, 0.85);
}

/* Selected Icon */
.ui.rating .icon.selected,
.ui.rating .icon.selected.active {
  color: rgba(0, 0, 0, 0.8);
}

/*-------------------
       Disabled
--------------------*/


/* disabled rating */
.ui.disabled.rating .icon {
  cursor: default;
}

/*-------------------
     Interacting (Active)
--------------------*/


/* Selected Rating */
.ui.rating.selected .active.icon {
  opacity: 0.5;
}
.ui.rating.selected .icon.selected,
.ui.rating .icon.selected {
  opacity: 1;
}


/*******************************
          Variations
*******************************/

.ui.mini.rating .icon {
  font-size: 0.7rem;
}
.ui.tiny.rating .icon {
  font-size: 0.8rem;
}
.ui.small.rating .icon {
  font-size: 0.875rem;
}
.ui.rating .icon {
  font-size: 1rem;
}
.ui.large.rating .icon {
  font-size: 1.1rem;
}
.ui.huge.rating .icon {
  font-size: 1.5rem;
}
.ui.massive.rating .icon {
  font-size: 2rem;
}


/*******************************
         Theme Overrides
*******************************/

@font-face {
  font-family: 'Rating';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'), url(data:application/font-woff;charset=utf-8;base64,) format('woff');
  font-weight: normal;
  font-style: normal;
}
.ui.rating .icon {
  font-family: 'Rating';
  line-height: 1;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  font-weight: normal;
  font-style: normal;
  text-align: center;
}

/* Empty Star */
.ui.rating .icon:before {
  content: '\f006';
}

/* Active Star */
.ui.rating .active.icon:before {
  content: '\f005';
}

/*-------------------
        Star
--------------------*/


/* Unfilled Star */
.ui.star.rating .icon:before {
  content: '\f005';
}

/* Active Star */
.ui.star.rating .active.icon:before {
  content: '\f005';
}

/* Partial */
.ui.star.rating .partial.icon:before {
  content: '\f006';
}
.ui.star.rating .partial.icon {
  content: '\f005';
}

/*-------------------
        Heart
--------------------*/


/* Empty Heart
.ui.heart.rating .icon:before {
  content: '\f08a';
}
*/
.ui.heart.rating .icon:before {
  content: '\f004';
}
/* Active */
.ui.heart.rating .active.icon:before {
  content: '\f004';
}


